
import React from 'react'

const UserInputforTemperature = props => (
    <table>
        <thead>
        <tr>
            <th>City</th>
        </tr>

        </thead>
    <tbody>
    { props.city.length > 0 ?
        props.city.map(city => (
            <tr key={city.id}>
                <td>{city.cityname}</td>
                <td>
                    <button className="button muted-button">Enter city</button>
                </td>
            </tr>)) : (
                <tr>
                    <td>No city entered</td>
                </tr>
                )}
            </tbody>
    </table>
)
export default UserInputforTemperature
